<template>
  <div>
    <Swiper>
      <Swipter-item v-for="item in banners">
        <a :href="item.link">
          <img :src="item.image" alt="" @load="imgloade">
        </a>
      </Swipter-item>
    </Swiper>
  </div>
</template>

<script>
    import {Swiper,SwipterItem} from '../../../components/public/Swipter/index'
  export default {
    name: "HomeSwipter",
    data(){
      return{
        isload:false
      }
    },
    props:{
      banners:{
        type:Array,
        default(){
          return []
        }
      }
    },
    components:{
      Swiper,
      SwipterItem
    },
    methods:{
      imgloade(){
        if(!this.isload){
          this.$emit('swipterImageLoad');
          this.isload = true
        }

      }
    }
  }
</script>

