<template>
  <div id="home">
    <nav-bar class="home-nav" isnavcolor="pink"><div slot="center">购物街</div></nav-bar>
    <Tab-control :titles="['流行','新款','精选']"
                 ref="tabControl1"
                 @tabclick="tabClick"
                 v-show="isTabFixed"
    />
    <Scroll class="content"
            ref="scroll"
            :probe-type="3"
            @scroll="contentscroll"
            :pull-up-load="true"
            @pullingUp="loadMore"
            >
      <Home-swipter :banners="banners"  @swipterImageLoad="swipterImageLoad"/>
      <Home-recommend-view :recommends="recommends" />
      <Home-feature-view/>
      <Tab-control :titles="['流行','新款','精选']"
                   ref="tabControl2"
                   @tabclick="tabClick"
                  />
      <Good-list  :goods="showGoods"/>
    </Scroll>
    <Back-top @click.native="backclick" v-show="isshow"/>
  </div>
</template>

<script>
  import NavBar from "../../components/public/TopBar/NavBar";
  import HomeSwipter from "./childrencomments/HomeSwipter";
  import HomeRecommendView from "./childrencomments/HomeRecommendView";
  import HomeFeatureView from "./childrencomments/HomeFeatureView";
  import GoodList from "../../components/public/Goods/GoodList";
  import Scroll from "../../components/public/scroll/Scroll";
  import BackTop from "../../components/public/BackTop/BackTop";


  import TabControl from "../../components/independent/tabcontrol/TabControl";

  import {gethomeMutidata,getHomeGoods} from "../../NetWork/home";
  import {debounce} from "../../components/public/utils/utils";

  import BScroll from 'better-scroll';




  export default {
    name: "Home",
    data(){
      return{
        banners:null,
        recommends:[],
        goods:{     //将请求的数据这么保存下来
          'pop':{page:0,list:[]},
          'new':{page:0,list:[]},
          'sell':{page:0,list:[]},
        },
        currenttype:'pop',
        isshow:false,
        taboffsetTop:0,
        isTabFixed:false,
        saveY:0,
        itemimagdata:null
      }
    },
    components:{
      NavBar,
      HomeSwipter,
      HomeRecommendView,
      HomeFeatureView,
      TabControl,
      GoodList,
      Scroll,
      BackTop
    },
    created() {
      //请求数据
      this.gethomeMutidata();
      //请求商品
      this.getHomeGoods('pop');
      this.getHomeGoods('new');
      this.getHomeGoods('sell');

    },
    mounted() {

    let refresh = debounce(  this.$refs.scroll.refresh,500);
      //监听图片加载完成
      this.itemimagdata = ()=>{
        // this.$refs.scroll.refresh();
        refresh();
      }
      this.$bus.$on('itemImageLoad',this.itemimagdata)

      //获取tabControl的offsetTop
      //所有的组件都有一个属性$el,获取组件内元素的

    },
    computed:{
      showGoods(){
        return this.goods[this.currenttype].list
      }
    },
    methods:{
      gethomeMutidata(){
        gethomeMutidata().then(res=>{
          this.banners = res.data.banner.list;
          this.recommends = res.data.recommend.list;
        });
      },
      getHomeGoods(type){
        let pages = this.goods[type].page + 1;
        getHomeGoods(type,pages).then(res=>{

          this.goods[type].list.push(...res.data.list)
          this.goods[type].page +=1
          this.$refs.scroll.finishPullUp()
        })
      },
      tabClick(idx){
        switch (idx) {
          case 0:
            this.currenttype = 'pop';
            break;
          case 1:
            this.currenttype = 'new';
            break;
          case 2:
            this.currenttype = 'sell';
            break;

        }
        this.$refs.tabControl1.currentIndex = idx;
        this.$refs.tabControl2.currentIndex = idx;
      },
      backclick(){
        // this.$refs.scroll.scroll.scrollTo(0,0,500);
        this.$refs.scroll.scrollTo(0,0)
      },
      contentscroll(position){
        // console.log(position)
        //判断backTop是否显示
        this.isshow = -position.y  > 1000;
        //2.决定tabConTrol是否吸顶
        /**
         *
         * @TODO  bug 吸顶效果
         */
        this.isTabFixed = (-position.y)>this.taboffsetTop
      },
      loadMore(){
        // console.log('上拉加载')
        this.getHomeGoods(this.currenttype)

        this.$refs.scroll.scroll.refresh()  //加载完重新计算高度
      },
      swipterImageLoad(){
        this.taboffsetTop = this.$refs.tabControl2.$el.offsetTop;
        // console.log(this.taboffsetTop)
      }

    },
   activated() {
      this.$refs.scroll.scrollTo(0,this.saveY,0);
      this.$refs.scroll.refresh()
     // console.log('进来')
   },
   deactivated() {
      this.saveY = this.$refs.scroll.scroll.y;
     // console.log('离开')
     //取消全局事件监听
     this.$bus.$off('itemImageLoad',this.itemimagdata)
   }

  }

</script>

<style scoped lang="scss">
  #home{

  }
  .home-nav{

    /*position: fixed;*/
    /*left: 0;*/
    /*right: 0;*/
    /*top: 0;*/
    /*z-index: 9;*/
  }
  /*.tab-control{*/
  /*  position: sticky;   //兼容性很拉闸*/
  /*  top: 44px;*/
  /*  background: white;*/
  /*  z-index: 9;*/
  /*}*/
  .content{
    height: calc(100vh - 98px);
    overflow: hidden;
    /*background: red;*/
  }

</style>