import {request,res} from "./requrest";

export  function gethomeMutidata() {
  return request({
    url:'/home/multidata',
    method:'get'
  })
}
//
export  function getHomeGoods(type,page) {
  return res({
    url:'/home/data',
    method: 'get',
    params:{
      type,
      page
    }
  })
}